.scrollCart {
  max-height: 700px;
  scrollbar-color: rgba(133, 133, 173, 1) rgba(47, 47, 55, 1);
  scrollbar-width: thin;
  overflow-y: scroll;
  padding-left: 8px;

  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.blockText {
  height: 150px;
  margin-top: 48px;
  width: 600px;
  display: flex;
  align-self: center;
}
